import { Component } from "@angular/core";
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { App } from '@services/app/app.api';
import { UserManager } from '@services/app/managers/user.manager';
import { AppService } from '@services/app/app.service';
import { finalize, mergeMap } from 'rxjs/operators';
import { NotificationsService } from 'angular2-notifications';
import { OrganizationManager } from '@services/app/managers/organization.manager';
import { Google } from '@common/third-parties/google';
import { Facebook } from '@common/third-parties/facebook';

declare const Stripe: any;

@Component({
    selector: 'app-settings',
    templateUrl: './settings.component.html',
    styleUrls: ['./settings.component.scss']
})
export class SettingsComponent {
    public get organization(): App.Organization { return OrganizationManager.sharedInstance.organization; }

    constructor(private app: AppService, private notifications: NotificationsService) { }

    public ngOnInit() {
        this.user = UserManager.sharedInstance.user;
        this.userForm = new FormGroup({});
        this.userForm.addControl('name', new FormControl(this.user.name, [Validators.required]));
        this.resetPasswordForm();

        this.app.API.getOrganizationSocialAccounts(this.organization).subscribe(accounts => {
            this.youtube = accounts.find(o => o.network === 'youtube');
            this.facebook = accounts.find(o => o.network === 'facebook');
        });

        this.cardForm = new FormGroup({});
        this.cardForm.addControl('number', new FormControl(undefined, [Validators.required]));
        this.cardForm.addControl('month', new FormControl(undefined, [Validators.required]));
        this.cardForm.addControl('year', new FormControl(undefined, [Validators.required]));
        this.cardForm.addControl('cvc', new FormControl(undefined, [Validators.required]));
        this.cardForm.addControl('zip', new FormControl(undefined, [Validators.required]));
        this.resetCardForm();

        this.app.API.getOrganizationSubscriptionInfo(this.organization).subscribe(info => {
            this.plan = (info.plans || []).find(o => o.id === info.account.planId);
            this.subscription = info.subscription;
            this.card = info.card;
            this.resetCardForm();
        });
    }

    public user: App.User;
    public userForm: FormGroup;
    public savingUserForm: boolean = false;

    public onSaveUserForm() {
        this.savingUserForm = true;
        this.app.API.updateUser({ id: this.user.id, name: this.userForm.controls.name.value.trim() }).pipe(finalize(() => {
            this.savingUserForm = false;
        })).subscribe(user => {
            this.user = UserManager.sharedInstance.user = user;
            this.notifications.success('Success', 'User profile saved successfully.', { timeOut: 5000 });
        }, error => {
            this.notifications.error('Error', 'Sorry, there was a problem saving.');
        });
    }

    public passwordDialogVisible: boolean = false;
    public passwordForm: FormGroup;
    public savingPasswordForm: boolean = false;

    private resetPasswordForm() {
        this.passwordForm = new FormGroup({});
        this.passwordForm.addControl('password', new FormControl(undefined, [Validators.required, Validators.minLength(8)]));
        this.passwordForm.addControl('confirm', new FormControl(undefined));
    }

    public onShowPasswordDialog() {
        this.resetPasswordForm();
        this.passwordDialogVisible = true;
    }

    public onSavePasswordForm() {
        let password = this.passwordForm.controls.password.value.trim();
        let confirm = this.passwordForm.controls.confirm.value.trim();
        if (password !== confirm) {
            this.notifications.error('Error', 'The password fields must match.');
            return;
        }

        this.savingPasswordForm = true;
        this.app.API.updateUser({ id: this.user.id, password: this.passwordForm.controls.password.value.trim() }).pipe(finalize(() => {
            this.savingPasswordForm = false;
        })).subscribe(user => {
            this.notifications.success('Success', 'Password saved successfully.', { timeOut: 5000 });
            this.passwordDialogVisible = false;
        }, error => {
            this.notifications.error('Error', 'Sorry, there was a problem saving.');
        });
    }

    public youtube: App.SocialAccount;
    public loadingYoutube: boolean = false;
    public facebook: App.SocialAccount;
    public loadingFacebook: boolean = false;

    public onConnectYoutube() {
        this.loadingYoutube = true;
        new Google(this.app.auth.http).userLogin().pipe(finalize(() => {
            this.loadingYoutube = false;
        }), mergeMap(account => {
            return this.app.API.setOrganizationSocialAccount(this.organization, account);
        })).subscribe(account => {
            this.notifications.success('Success', 'Connected YouTube successfully.', { timeOut: 5000 });
            this.youtube = account;
        }, error => {
            if (typeof error === 'string' && error.indexOf('cancel')) { return; }
            this.notifications.error('Error', 'Sorry, there was a problem connecting.');
        });
    }

    public onDisconnectYoutube() {
        this.loadingYoutube = true;
        this.app.API.deleteOrganizationSocialAccount(this.organization, 'youtube').pipe(finalize(() => {
            this.loadingYoutube = false;
        })).subscribe(() => {
            this.notifications.success('Success', 'Disconnected YouTube successfully.', { timeOut: 5000 });
            this.youtube = undefined;
        }, error => {
            this.notifications.error('Error', 'Sorry, there was a problem disconnecting.');
        })
    }

    public onConnectFacebook() {
        this.loadingFacebook = true;
        new Facebook(this.app.auth.http).userLogin().pipe(finalize(() => {
            this.loadingFacebook = false;
        }), mergeMap(account => {
            return this.app.API.setOrganizationSocialAccount(this.organization, account);
        })).subscribe(account => {
            this.notifications.success('Success', 'Connected Facebook successfully.', { timeOut: 5000 });
            this.facebook = account;
        }, error => {
            if (typeof error === 'string' && error.indexOf('cancel')) { return; }
            this.notifications.error('Error', 'Sorry, there was a problem connecting.');
        });
    }

    public onDisconnectFacebook() {
        this.loadingFacebook = true;
        this.app.API.deleteOrganizationSocialAccount(this.organization, 'facebook').pipe(finalize(() => {
            this.loadingFacebook = false;
        })).subscribe(() => {
            this.notifications.success('Success', 'Disconnected Facebook successfully.', { timeOut: 5000 });
            this.facebook = undefined;
        }, error => {
            this.notifications.error('Error', 'Sorry, there was a problem disconnecting.');
        })
    }

    public plan: App.StripePlan;
    public subscription: App.StripeSubscription;
    public card: App.StripeCard;

    public cardNumberPlaceholder: string;
    public cardMonthPlaceholder: string;
    public cardYearPlaceholder: string;
    public cardCvcPlaceholder: string;
    public cardZipPlaceholder: string;

    public cardForm: FormGroup;
    public savingCardForm: boolean = false;

    public onUpdatePaymentInfo() {
        let number = this.cardForm.controls.number.value;
        let month = this.cardForm.controls.month.value;
        let year = this.cardForm.controls.year.value;
        let cvc = this.cardForm.controls.cvc.value;
        let zip = this.cardForm.controls.zip.value;

        this.savingCardForm = true;
        Stripe.card.createToken({
            'number': number,
            'exp_month': month,
            'exp_year': year,
            'cvc': cvc,
            'address_zip': zip,
        }, (status, response) => {
            if (!status || status >= 300 || status < 200) {
                this.savingCardForm = false;
                this.notifications.error('Error', 'Sorry, there was a problem updating your payment info.');
                return;
            }

            this.app.API.updateOrganizationPaymentInfo(this.organization, response.id, this.plan.id).pipe(finalize(() => {
                this.savingCardForm = false;
            })).subscribe(info => {
                this.notifications.success('Success', 'Payment info updated successfully.', { timeOut: 5000 });
                this.subscription = info.subscription;
                this.card = info.card;
                this.resetCardForm();
            }, error => {
                this.notifications.error('Error', 'Sorry, there was a problem updating your payment info.');
            });
        });
    }

    public cancelDialogVisible: boolean = false;
    public canceling: boolean = false;

    public onCancelSubscription() {
        this.canceling = true;
        this.app.API.deleteOrganizationPaymentInfo(this.organization).pipe(finalize(() => {
            this.canceling = false;
        })).subscribe(info => {
            this.notifications.success('Success', 'Your subscription has been canceled.', { timeOut: 5000 });
            this.subscription = info.subscription;
            this.card = info.card;
            this.resetCardForm();
            this.cancelDialogVisible = false;
        }, error => {
            this.notifications.error('Error', 'Sorry, there was a problem canceling your subscription.');
        });
    }

    private resetCardForm() {
        this.cardNumberPlaceholder = this.card?.last4 ? `**** **** **** ${this.card.last4}` : `0000 0000 0000 0000`;
        this.cardMonthPlaceholder = `${this.card?.exp_month || 'MM'}`;
        if (this.cardMonthPlaceholder.length === 1) { this.cardMonthPlaceholder = `0${this.cardMonthPlaceholder}`; }
        this.cardYearPlaceholder = `${this.card?.exp_year || 'YYYY'}`.substring(2);
        if (this.cardYearPlaceholder.length === 1) { this.cardYearPlaceholder = `0${this.cardYearPlaceholder}`; }
        this.cardCvcPlaceholder = this.card ? '***' : '000';
        this.cardZipPlaceholder = this.card?.address_zip || '00000';

        this.cardForm.controls.number.setValue(undefined);
        this.cardForm.controls.month.setValue(undefined);
        this.cardForm.controls.year.setValue(undefined);
        this.cardForm.controls.cvc.setValue(undefined);
        this.cardForm.controls.zip.setValue(undefined);
    }
}
