import { BehaviorSubject, throwError, of } from 'rxjs';
import { Injectable } from "@angular/core";
import { map } from 'rxjs/operators';
import { mergeMap } from 'rxjs/operators';
import { Observable } from "rxjs";
import { Subject } from 'rxjs';
import { App } from '@services/app/app.api';
import { HttpClient, HttpResponse } from '@angular/common/http';

export const NETWORK = 'youtube';
const GOOGLE_AUTH_URL = 'https://accounts.google.com/o/oauth2/v2/auth';
const GOOGLE_API_URL = 'https://www.googleapis.com';

const OAUTH_CLIENT_ID = '411673316996-iak5hlp5od4pvefhlc9ephasmp9dgo6f.apps.googleusercontent.com';
const OAUTH_CLIENT_SECRET = '2BQeOxl-YIAo-HjLLz-hxorg';

const REDIRECT_URI_LOCAL = 'http://localhost:8080';

const SCOPES = [
    'https://www.googleapis.com/auth/youtube.upload',
    'https://www.googleapis.com/auth/youtube'
];

interface Credentials {
    accessToken: string;
    refreshToken?: string;
}

interface PendingRequest {
    method: string;
    path: string;
    params: { [key: string]: string | Array<string> };
    headers: { [key: string]: string };
    data?: any;
}

class PendingSubject extends Subject<HttpResponse<any>> {
    constructor(public request: PendingRequest) { super(); }
}

@Injectable()
export class Google {
    private credentials: Credentials;
    public loggedIn$: BehaviorSubject<boolean> = new BehaviorSubject(false);
    public get accessToken(): string { return this.credentials && this.credentials.accessToken; }

    constructor(private http: HttpClient) { }

    public userLogin(): Observable<App.SocialAccount> {
        return this.getAuthCodeLocal().pipe(mergeMap(code => {
            return this.oauthToken({
                'grant_type': 'authorization_code',
                'redirect_uri': REDIRECT_URI_LOCAL,
                'code': code,
            });
        }), map(credentials => {
            let account = new App.SocialAccount();
            account.network = NETWORK;
            account.accessToken = credentials.accessToken;
            account.refreshToken = credentials.refreshToken;
            return account;
        }));
    }

    private getAuthCodeLocal(): Observable<string> {
        let result = new Subject<string>();

        let params = new URLSearchParams();
        params.set('client_id', OAUTH_CLIENT_ID);
        params.set('redirect_uri', REDIRECT_URI_LOCAL);
        params.set('scope', SCOPES.join(' '));
        params.set('response_type', 'code');
        params.set('prompt', 'select_account');
        let url = GOOGLE_AUTH_URL + '?' + params.toString();

        // center window
        // var left = (screen.width/2)-(w/2);
        // var top = (screen.height/2)-(h/2);
        // return window.open(url, title, 'toolbar=no, location=no, directories=no, status=no, menubar=no, scrollbars=no, resizable=no, copyhistory=no, width='+w+', height='+h+', top='+top+', left='+left);

        let ref = window.open(url, '_blank', 'width=500,height=500,menubar=no');

        let code: string = undefined;
        let interval = setInterval(() => {
            try {
                let href = ref.location.href;
                if (href.indexOf('?') >= 0) {
                    let search = new URLSearchParams(href.split('?')[1]);
                    code = search.get('code');
                    ref.close();
                }
            } catch (e) { }

            if (ref.closed) {
                clearInterval(interval);
                if (code) {
                    result.next(code);
                    result.complete();
                } else {
                    result.error('Authorization canceled');
                }
            }
        }, 500);

        return result;
    }

    private oauthToken(params_: { [key: string]: string }): Observable<Credentials> {
        let headers: { [key: string]: string | Array<string> } = {
            'Accept': 'application/json',
            'Content-Type': 'application/x-www-form-urlencoded',
        };

        let params = new URLSearchParams();
        params.set('client_id', OAUTH_CLIENT_ID);
        params.set('client_secret', OAUTH_CLIENT_SECRET);

        let _params: { [key: string]: string } = params_ || {};
        for (let key in _params) { params.set(key, _params[key]); }

        return this.http.request('POST', 'https://www.googleapis.com/oauth2/v4/token', {
            body: params.toString(),
            headers: headers,
            responseType: 'json',
            observe: 'response',
        }).pipe(map(response => {
            const json: any = response.body;
            return { 'accessToken': json.access_token, 'refreshToken': json.refresh_token };
        }));
    }

    public userLogout(): Observable<void> {
        this.credentials = undefined;
        this.loggedIn$.next(false);
        return of(true).pipe(map(() => { }));
    }

    public restoreLoggedInUser(account: App.SocialAccount): Observable<void> {
        this.credentials = { accessToken: account.accessToken, refreshToken: account.refreshToken };
        this.loggedIn$.next(true);
        return of(true).pipe(map(() => { }));
    }

    private userRefresh(token: string): Observable<void> {
        return this.oauthToken({
            'grant_type': 'refresh_token',
            'refresh_token': token
        }).pipe(map(credentials => {
            this.credentials = credentials;
            this.loggedIn$.next(true);
        }));
    }

    private request(method: string, path: string, params_: { [key: string]: string | Array<string> }, headers_: { [key: string]: string }, data: any): Observable<HttpResponse<any>> {
        let headers: { [key: string]: string | Array<string> } = { ...(headers_ || {}) };

        if (!!data && !headers['Content-Type']) { headers['Content-Type'] = 'application/json'; }
        if (!headers['Accept']) { headers['Accept'] = 'application/json'; }

        let responseType: 'blob' | 'json' | 'text' =
            headers['Accept'] === 'application/octet-stream' ? 'blob' : (headers['Accept'] === 'application/json' ? 'json' : 'text');

        let credentials = this.credentials;
        if (credentials && !headers['Authorization']) { headers['Authorization'] = `Bearer ${credentials.accessToken}`; }

        // Workaround https://github.com/angular/angular/issues/11058
        let params: { [key: string]: string | Array<string> } = { ...(params_ || {}) };
        for (let key in params) {
            if (params[key] === undefined) {
                delete params[key];
            } else {
                let array: Array<any> = Array.isArray(params[key]) ? params[key] as Array<string> : [params[key] as string];
                array = array.map(o => {
                    if (!((o as any) instanceof Date) || !o.getTime()) { return o; }
                    return o.toISOString(); // serialize dates in a specific way
                }).filter(o => o !== undefined);
                if (!array.length) {
                    delete params[key];
                } else {
                    params[key] = array.length === 1 ? array[0] : array;
                }
            }
        }

        let url = GOOGLE_API_URL + path;

        let subject = new Subject<HttpResponse<any>>();
        this.http.request(method, url, {
            body: data,
            headers: headers,
            params: params,
            responseType: responseType,
            observe: 'response',
        }).subscribe(response => {
            subject.next(response);
            subject.complete();
        }, response => {
            this.fail(response, { 'method': method, 'path': path, 'params': params_, 'headers': headers_, 'data': data }).subscribe(subject);
        });

        return subject;
    }

    public GET(path: string, params: { [key: string]: string | Array<string> }, headers: { [key: string]: string } = {}, data: any = undefined): Observable<HttpResponse<any>> {
        return this.request('GET', path, params, headers, data);
    }

    public POST(path: string, params: { [key: string]: string | Array<string> }, headers: { [key: string]: string } = {}, data: any = undefined): Observable<HttpResponse<any>> {
        return this.request('POST', path, params, headers, data);
    }

    public PUT(path: string, params: { [key: string]: string | Array<string> }, headers: { [key: string]: string } = {}, data: any = undefined): Observable<HttpResponse<any>> {
        return this.request('PUT', path, params, headers, data);
    }

    public DELETE(path: string, params: { [key: string]: string | Array<string> }, headers: { [key: string]: string } = {}, data: any = undefined): Observable<HttpResponse<any>> {
        return this.request('DELETE', path, params, headers, data);
    }

    private fail(response: HttpResponse<any>, request: PendingRequest): Observable<HttpResponse<any>> {
        return this.shouldAttemptRequestAgainAfterRefreshing(response, request) || throwError(response);
    }

    private pending: Array<PendingSubject> = undefined;
    private shouldAttemptRequestAgainAfterRefreshing(response: HttpResponse<any>, request: PendingRequest): Observable<HttpResponse<any>> {
        var result: Observable<HttpResponse<any>> = undefined;

        let json = undefined;
        try { json = response && ((response as any).error || response.body); } catch (error) { }
        let error = json && json.error && json.error.message && json.error.message.toLowerCase() || '';
        if (error.indexOf('credentials') >= 0) {
            let subject = new PendingSubject(request);
            result = subject;

            if (!this.pending) {
                this.pending = new Array<PendingSubject>();
                this.pending.push(subject);
                this.processRefresh();
            } else {
                this.pending.push(subject);
            }
        }

        return result;
    }

    private processRefresh() {
        let credentials = this.credentials;
        if (!credentials || !credentials.accessToken) {
            this.userLogout();
            this.completeRefresh();
            return;
        }

        if (!!credentials.refreshToken) {
            this.userRefresh(credentials.refreshToken).subscribe(response => {
                this.completeRefresh();
            }, response => {
                this.userLogout();
                this.completeRefresh();
            });
        } else {
            this.userLogout();
            this.completeRefresh();
        }
    }

    private completeRefresh() {
        let pending = this.pending;
        this.pending = undefined;

        if (!pending) { return; }

        let credentials = this.credentials;
        for (let subject of pending) {
            if (credentials && credentials.accessToken) {
                let request = subject.request;
                this.request(request.method, request.path, request.params, request.headers, request.data).subscribe(subject);
            } else {
                throwError({
                    'statusText': 'Invalid access token.',
                    'status': 401
                }).subscribe(subject);
            }
        }
    }
}
